	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"Erlang Factory SF Bay Area 2013\nMore info and slides on the website: http://www.erlang-factory.com/conference/SFBay2013/talks\n\nWe all know that Erlang is great for building complicated distributed systems, but did you know that building a website in Erlang is as easy as using Ruby on Rails? In this talk I'll introduce you to Chicago Boss, the web framework that takes care of all the hard parts of Erlang/OTP for you, letting the developer focus on building a great website. I'll explain why Erlang is an excellent choice for delivering web content, give a high-level overview of the Chicago Boss architecture, and explicate the philosophy behind some of CB's controversial design decisions. In the process we'll cover CB's high-performance template system, its custom compiler chain and language-integrated query syntax, its unique approaches to e-mail and i18n, and everything in between. Some practical code examples will be included at the end. All levels of expertise are welcome.","meta":{"duration":"PT45M59S"},"presenter":{"name":null},"source":{"id":"LGGo6bIuj8w","provider":"youtube"},"slug":"chicago-boss-a-web-framework-built-for-comfort-and-speed-evan-miller","tag":["Erlang","Erlang Factory","Chicago Boss","Evan Miller","Ruby on Rails","Web Framework"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/LGGo6bIuj8w/default.jpg","height":90,"width":120}},"title":"Chicago Boss: A Web Framework Built for Comfort (and Speed) - Evan Miller"}},{"node":{"description":"Erlang Factory SF Bay Area 2013\nMore info and slides on the website: http://www.erlang-factory.com/conference/SFBay2013/talks\n\nSoftware is difficult because the parts don't fit together. Why is this? Can we do anything about this? And what's this got to do with Erlang? Come to my talk and you'll find out!","meta":{"duration":"PT46M40S"},"presenter":{"name":null},"source":{"id":"ed7A7r6DBsM","provider":"youtube"},"slug":"the-how-and-why-of-fitting-things-together-joe-armstrong","tag":["Erlang","Erlang Factory","Joe Armstrong","Keynote"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/ed7A7r6DBsM/default.jpg","height":90,"width":120}},"title":"The How and Why of Fitting Things Together - Joe Armstrong"}}]}},"pathContext":{"slug":"erlang-factory","tag":"Erlang Factory"}}

